<template>
  <div>
    <!-- 轉至急性醫院住院人數 -->
    <v-alert outlined>
      <h1 class="text-center">
        {{ setYear - 1911 }}年 轉至急性醫院住院人數
      </h1>
      <VeHistogram :data="chartData1"></VeHistogram>
    </v-alert>

    <!-- 非計畫性轉至急性醫院住院分類 -->
    <v-alert outlined>
      <h1 class="text-center">
        {{ setYear - 1911 }}年 非計畫性轉至急性醫院住院分類
      </h1>
      <VeHistogram :data="chartData2"></VeHistogram>
    </v-alert>

    <!-- 全體住院比率 -->
    <v-alert outlined>
      <h1 class="text-center">
        {{ setYear - 1911 }}年 全體住院比率
      </h1>
      <VeHistogram
        :data="chartData3"
        :settings="chartSetNormal"
      ></VeHistogram>
    </v-alert>

    <!-- 住院比率 -->
    <v-alert outlined>
      <h1 class="text-center">
        {{ setYear - 1911 }}年 住院比率
      </h1>
      <VeHistogram
        :data="chartData4"
        :settings="chartSetNormal"
      ></VeHistogram>
    </v-alert>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear','list'],

  data(){
    return {
      // 圖表設定及值
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },

  computed:{
    //圖表1的值
    chartData1() {
      return {
        columns: [
          "月份",
          "計畫性轉至急性醫院住院",
          "非計畫性轉至急性醫院住院",
          "入住後72小時內非計畫性轉至急性醫院住院",
        ],
        rows: this.list.monthLists.map((i, index) => {
          return {
            月份: (index+1) + "月",
            計畫性轉至急性醫院住院: i.d,
            非計畫性轉至急性醫院住院: i.e,
            入住後72小時內非計畫性轉至急性醫院住院: i.g,
          };
        }),
      };
    },
    chartData2() {
      return {
        columns: [
          "月份",
          "因心血管代償機能減退",
          "因骨折之治療或評估",
          "因腸胃道出血",
          "因感染",
          "因其他內外科原因",
        ],
        rows: this.list.monthLists.map((i, index) => {
          return {
            月份: (index+1) + "月",
            因心血管代償機能減退: i.i1,
            因骨折之治療或評估: i.i2,
            因腸胃道出血: i.i3,
            因感染: i.i4,
            因其他內外科原因: i.i5,
          };
        }),
      };
    },
    //圖表3的值
    chartData3() {
      return {
        columns: ["月份", "全體住院比率"],
        rows: this.list.monthLists.map((i, index) => {
          return {
            月份: (index+1) + "月",
            全體住院比率: i.b ? i.b / i.a : 0,
          };
        }),
      };
    },
    //圖表4的值
    chartData4() {
      return {
        columns: ["月份", "計畫性住院比率","非計畫性住院比率"],
        rows: this.list.monthLists.map((i, index) => {
          return {
            月份: (index+1) + "月",
            計畫性住院比率: i.d ? i.d / i.b : 0,
            非計畫性住院比率: i.e ? i.e / i.b : 0,
          };
        }),
      };
    },
  }
}
</script>