<template>
  <div>
    <!-- 跌倒事件 月,季,年 -->
    <v-tabs v-model="tab" background-color="transparent" color="indigo" grow>
        <v-tab v-for="item in TabItems" :key="item">
          <h2>{{ item }}</h2>
        </v-tab>
      </v-tabs>

      <v-tabs-items v-model="tab">
        <v-tab-item> <monthAnalysis class="pa-3" :items="items"/> </v-tab-item>
        <v-tab-item> <seasonAnalysis class="pa-3" :items="items"/> </v-tab-item>
        <v-tab-item> <yearAnalysis class="pa-3" :items="items"/> </v-tab-item>
      </v-tabs-items>
  </div>
</template>

<script>
import monthAnalysis from "./monthAnalysis.vue";
import seasonAnalysis from "./seasonAnalysis.vue";
import yearAnalysis from "./yearAnalysis.vue";

export default {
  components:{monthAnalysis,seasonAnalysis,yearAnalysis},
  data() {
    return {
      tab: null,
      TabItems: [
        "月報表",
        "季報表",
        "年報表",
      ],
      items:[],
      useDataBase: 'fallevent',  //跌倒事件資料庫
    }
  },

  mounted(){
    this.getAllData();
  },

  methods:{
    getAllData() {
      this.$api.options(
        "general/getAll/" + this.$store.state.databaseName + "/" + this.useDataBase
      ).then((rs) => {
        this.items = rs
          .map((i) => {
            return {
              ...JSON.parse(i.datalist),
              snkey: i.snkey,
            };
          })
      });
    },
    
  }
}
</script>