<template>
  <div class="ConstraintEventAnalysisMonth">
    <div class="d-flex justify-space-between align-center">
      <v-text-field
        type="month"
        label="選擇年/月"
        v-model="setMonth"
        hide-details
      ></v-text-field>

      <v-spacer></v-spacer>
    </div>
    <div v-if="dataReady">
      <!-- {{ PDValue }} -->
      <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">數據</th>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold">件數</th>
            <th class="title font-weight-bold">公式</th>
            <th class="title font-weight-bold">比率(%)</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>--</td>
            <td>當月住民總人數(A)</td>
            <td>{{ list.a }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>1</td>
            <td>當月全體住民轉至急性醫院住院人次(B):轉至急性醫院住院比率</td>
            <td>{{ list.b }}</td>
            <td>B/A</td>
            <td>{{ list.b ? ((list.b / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>當月住民總人日數(C)</td>
            <td>{{ list.c }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>2</td>
            <td>
              當月全體住民轉至急性醫院住院人次(B):轉至急性醫院住院密度
            </td>
            <td>{{ list.b }}</td>
            <td>B/C</td>
            <td>{{ list.b ? ((list.b / list.c) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>
              當月轉至急性醫院住院住民之總人數(D)
            </td>
            <td>{{ list.d }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>3</td>
            <td>當月非計畫性轉至急性醫院住院人次(E):非計畫性轉至急性醫院住院比率</td>
            <td>{{ list.e }}</td>
            <td>E/D</td>
            <td>{{ list.e ? ((list.e / list.d) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>當月新住民人數(F)</td>
            <td>{{ list.f }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>4</td>
            <td>當月新住民入住後72小時內非計畫性轉至急性醫院住院人次(G)；新住民入住後72小時內非計畫性轉至急性醫院住院比率</td>
            <td>{{ list.g }}</td>
            <td>G/F</td>
            <td>{{ list.g ? ((list.g / list.f) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>當月非計畫性轉至急性醫院住院人數(H)</td>
            <td>{{ list.h }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>5.1</td>
            <td>當月因心血管代償機能減退而非計畫性至急性醫院住院人次(I1)；因心血管代償機能減退而非計畫性至急性醫院住院比率</td>
            <td>{{ list.i1 }}</td>
            <td>I1/H</td>
            <td>{{ list.i1 ? ((list.i1 / list.h) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>5.2</td>
            <td>當月因骨折之治療或評估而非計畫性至急性醫院住院人次(I2)；因骨折之治療或評估而非計畫性至急性醫院住院比率</td>
            <td>{{ list.i2 }}</td>
            <td>I2/H</td>
            <td>{{ list.i2 ? ((list.i2 / list.h) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>5.3</td>
            <td>當月因腸胃道出血而非計畫性轉至急性醫院住院人次(I3)；因腸胃道出血而非計畫性轉至急性醫院住院比率</td>
            <td>{{ list.i3 }}</td>
            <td>I3/H</td>
            <td>{{ list.i3 ? ((list.i3 / list.h) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>5.4</td>
            <td>當月因感染而非計畫性至急性醫院住院人次(I4)；因感染而非計畫性至急性醫院住院比率</td>
            <td>{{ list.i4 }}</td>
            <td>I4/H</td>
            <td>{{ list.i4 ? ((list.i4 / list.h) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>5.5</td>
            <td>當月因其他內外科原因而非計畫性至急性醫院住院人次(I5)；因其他內外科原因而非計畫性至急性醫院住院比率</td>
            <td>{{ list.i5 }}</td>
            <td>I5/H</td>
            <td>{{ list.i5 ? ((list.i5 / list.h) * 100).toFixed(0) : 0 }} %</td>
          </tr>

        </tbody>
      </v-simple-table>
      <v-textarea label="評估分析" auto-grow v-model="list.note"></v-textarea>

      <div class="d-flex justify-space-between">
        <v-btn class="error" @click.stop="delData" v-if="workProcess == 'edit'"
          >刪除目前資料</v-btn
        >
        <v-spacer></v-spacer>
        <v-btn
          class="primary"
          @click.stop="saveData"
          v-if="workProcess == 'add'"
          :loading="loading"
          :disabled="loading"
          >資料存檔</v-btn
        >
        <v-btn
          class="success"
          @click.stop="saveData"
          v-if="workProcess == 'edit'"
          :loading="loading"
          :disabled="loading"
          >資料更新</v-btn
        >
      </div>
    </div>
    <!-- {{items}} -->
  </div>
</template>

<script>
import dayjs from "dayjs";
import PDVFn from "@/assets/js/PDVFn.js";

export default {
  props: ["items"], //items全部約束事件資料
  data() {
    return {
      useDataBase : 'analysisofevaluationindicators',
      list: {},
      setMonth: dayjs().format("YYYY-MM"),
      workProcess: "add", //判斷目前作業為新增或修改
      loading: false,
    };
  },

  computed: {
    //判斷資料都已讀入
    dataReady() {
      if (
        this.$store.state.userItems.length &&
        this.$store.state.bed_usedItems.length &&
        this.$store.state.behospitalizedItems.length &&
        this.$store.state.analysisofevaluationindicatorsItems
      ) {
        this.checkType(); //判斷新增/修改
        return true;
      } else {
        console.log("not yet");
        return false;
      }
    },
  },

  methods: {
    //判斷新增/修改
    checkType() {
      let match = this.$store.state.analysisofevaluationindicatorsItems
        .map((i) => {
          return {
            snkey: i.snkey,
            ...JSON.parse(i.datalist),
          };
        })
        .find((i) => {
          return i.range == this.setMonth && i.rangeType == "month" && i.analysisKind == this.analysisKind;
        });
      if (match) {
        //資料存在->修改
        this.workProcess = "edit";
        this.list = match;
      } else {
        //新增
        this.workProcess = "add";
        this.newData();
      }
    },
    // 顯示分析結果 之後需要存檔 -> 要做判斷 ? 好像.都直接算出來就好了.
    newData() {
      this.list = PDVFn.returnMonthDataUnplannedEvent(this.items, this.setMonth);
    },
    //新增/修改
    saveData() {
      this.loading = true;
      if (this.workProcess == "add") {
        //新增
        this.list.range = this.setMonth;
        this.list.rangeType = "month";
        this.list.create_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          datalist: JSON.stringify(this.list),
        };
        var url = "general/add/";
        this.workProcess = "edit";
      } else {
        //修改
        if (!this.list.edit_man) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man;
        var postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: "pass",
        };
        var url = "general/edit/";
      }

      this.$api.options(
        url + this.$store.state.databaseName + "/" + this.useDataBase,
        postData
      ).then((rs) => {
        console.log(rs);
        if (rs.state == 1) {
          var pop = { msg: "已存檔", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.loading = false;
          this.$store.dispatch("getItems", "analysisofevaluationindicators");  //更新已存檔的分析資料
        }
      });
    },
    //刪除功能
    delData() {
      let delData = this.list;
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: this.useDataBase,
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.newData();
              this.workProcess = "add";
              this.$store.dispatch("getItems", "analysisofevaluationindicators");  //更新已存檔的分析資料
            }
          });
        }
      });
    },
  },
};
</script>

<style scoped>
.v-data-table td {
  font-size: 1rem;
}
</style>