<template>
  <div>
    <!-- 壓力性損傷點盛行率 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年 壓力性損傷點盛行率</h1>
        <VeLine :data="chartData1" :settings="chartSetNormal"></VeLine>
      </v-alert>

      <!-- 各級壓力性損傷點盛行率 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年 各級壓力性損傷點盛行率</h1>
        <VeHistogram :data="chartData2" :settings="chartSetNormal"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month">{{month}} 月</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>
              一級
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b1 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              二級
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b2 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              三級
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b3 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td>四級</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b4 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>

      <!-- 壓力性損傷人數 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年 跌倒傷害嚴重度</h1>
        <VeHistogram :data="chartData3" :settings="chartSetNormal"></VeHistogram>

      </v-alert>
      <!-- 重複跌倒比率 -->
      <!-- <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年 重複跌倒比率</h1>
        <VeHistogram :data="chartData4" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month">{{month}} 月</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>重複跌倒比率</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e ? ((list.e / list.d) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert> -->
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear','list'],

  data(){
    return {
      // 圖表設定及值
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },

  computed:{
    //圖表1的值
    chartData1(){
      return {
        columns: ["月份", "點盛行率"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : (index+1) + '月',
            '點盛行率' : i.b ? (i.b / i.a).toFixed(4) : 0,
          }
        })
      }
    },
    //圖表2的值
    chartData2(){
      return {
        columns: ["月份", "一級", "二級", "三級" ,"四級"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : (index+1) +'月',
            '一級' : i.a ? (i.b1 / i.a) : 0,
            '二級' :i.a ? (i.b2 / i.a) : 0,
            '三級' : i.a ? (i.b3 / i.a) : 0,
            '四級' : i.a ? (i.b4 / i.a) : 0,
          }
        })
      }
    },
    //圖表3的值
    chartData3(){
      return {
        columns: ["月份", "發生率"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : (index+1) +'月',
            '發生率' : i.d ? (i.d / i.c) : 0,
          }
        })
      }
    },
    //圖表4的值
    // chartData4(){
    //   return {
    //     columns: ["月份", "比率"],
    //     rows: this.list.monthLists.map((i,index)=>{
    //       return {
    //         '月份' : (index+1) +'月',
    //         '比率' : i.e ? (i.e / i.d) : 0,
    //       }
    //     })
    //   }
    // },
  }
}
</script>