<template>
  <div>
    <!-- 總感染發生密度 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年 總感染發生密度</h1>
        <VeLine :data="chartData1" :settings="chartSetNormal"></VeLine>
      </v-alert>

      <!-- 感染發生人數 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年 感染人數</h1>
        <VeHistogram :data="chartData3" ></VeHistogram>
      </v-alert>

      <!-- 各類感染發生密度 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年 各類感染發生密度</h1>
        <VeHistogram :data="chartData2" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month">{{month}} 月</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>呼吸道感染</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c ? ((list.c / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>下呼吸道感染</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c1 ? ((list.c1 / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>泌尿道感染</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d ? ((list.d / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>使用存留導尿管的泌尿道感染</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d3 ? ((list.d3 / list.d1) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>未使用存留導尿管的泌尿道感染</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d4 ? ((list.d4 / list.d2) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>皮膚感染</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e ? ((list.e / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>皮膚及軟組織感染(蜂窩性組織炎/軟組織/傷口感染)</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e1 ? ((list.e1 / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>疥瘡感染</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e2 ? ((list.e2 / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>腸胃道感染</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.f ? ((list.f / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>

  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear','list'],

  data(){
    return {
      // 圖表設定及值
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        digit: 3,
      },
    }
  },

  computed:{
    //圖表1的值
    chartData1(){
      return {
        columns: ["月份", "發生密度"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : (index+1) + '月',
            '發生密度' : i.b ? (i.b / i.a).toFixed(4) : 0,
          }
        })
      }
    },
    //圖表2的值
    chartData2(){
      return {
        columns: ["月份", 
        "呼吸道感染", 
        "下呼吸道感染", 
        "泌尿道感染" ,
        "使用存留導尿管的泌尿道感染",
        "未使用存留導尿管的泌尿道感染",
        "皮膚感染",
        "皮膚及軟組織感染(蜂窩性組織炎/軟組織/傷口感染)",
        "疥瘡感染",
        "腸胃道感染"
        ],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : (index+1)+'月',
            '呼吸道感染' : i.c ? (i.c / i.a) : 0,
            '下呼吸道感染' :i.c1 ? (i.c1 / i.a) : 0,
            '泌尿道感染' : i.d ? (i.d / i.a) : 0,
            '使用存留導尿管的泌尿道感染' : i.d3 ? (i.d3 / i.d1) : 0,
            '未使用存留導尿管的泌尿道感染' : i.d4 ? (i.d4 / i.d2) : 0,
            '皮膚感染' : i.e ? (i.e / i.a) : 0,
            '皮膚及軟組織感染(蜂窩性組織炎/軟組織/傷口感染)' : i.e1 ? (i.e1 / i.a) : 0,
            '疥瘡感染' : i.e2 ? (i.e2 / i.a) : 0,
            '腸胃道感染' : i.f ? (i.f / i.a) : 0,
          }
        })
      }
    },
    //圖表3的值
    chartData3(){
      return {
        columns: ["月份", "人數"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : (index+1)+'月',
            '人數' : i.b,
          }
        })
      }
    },
  }
}
</script>