<template>
  <div>
      <!-- 身體約束發生密度 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年第{{ setSeason.season }}季身體約束發生密度</h1>
        <VeLine :data="chartData1" :settings="chartSetNormal"></VeLine>
      </v-alert>

      <!-- 身體約束發生人數 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年第{{ setSeason.season }}季身體約束發生人數</h1>
        <VeHistogram :data="chartData2" ></VeHistogram>
      </v-alert>

      <!-- 各類身體約束發生密度 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年第{{ setSeason.season }}季各類身體約束發生密度</h1>
        <VeHistogram :data="chartData3" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold" v-for="month in setSeason.months" :key="month.season">{{month}} 月</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>預防跌倒</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c1 ? ((list.c1 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>預防自拔管路</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c2 ? ((list.c2 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>預防自傷</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c3 ? ((list.c3 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>行為紊亂</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c4 ? ((list.c4 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>協助治療</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c5 ? ((list.c5 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>其他</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c6 ? ((list.c6 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>

        </tbody>
      </v-simple-table>
      </v-alert>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear', 'setSeason' ,'list'],

  data(){
    return {
      // 圖表設定及值
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },

  computed:{
    //圖表1的值
    chartData1(){
      return {
        columns: ["月份", "身體約束發生密度"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : this.setSeason.months[index]+'月',
            '身體約束發生密度' : i.b ? (i.b / i.a).toFixed(4) : 0,
          }
        })
      }
    },
    //圖表2的值
    chartData2(){
      return {
        columns: ["月份", "人數","移除身體約束至少維持24小時以上"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : this.setSeason.months[index]+'月',
            '人數' : i.b,
            '移除身體約束至少維持24小時以上' : i.f
          }
        })
      }
    },
    //圖表3的值
    chartData3(){
      return {
        columns: 
        ["月份", 
        "預防跌倒", 
        "預防自拔管路", 
        "預防自傷" ,
        "行為紊亂",
        "協助治療",
        "其他",
        ],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : this.setSeason.months[index]+'月',
            '預防跌倒' : i.c1 ? (i.c1 / i.b) : 0,
            '預防自拔管路' :i.c2 ? (i.c2 / i.b) : 0,
            '預防自傷' : i.c3 ? (i.c3 / i.b) : 0,
            '行為紊亂' : i.c4 ? (i.c4 / i.b) : 0,
            '協助治療' : i.c5 ? (i.c5 / i.b) : 0,
            '其他' : i.c6 ? (i.c6 / i.b) : 0,
          }
        })
      }
    },
  }
}
</script>