<template>
  <div class="ConstraintEventAnalysisSeason">
    <div class="d-flex justify-space-between align-center">
      <v-text-field
        label="選擇年"
        :rules="rules"
        maxlength="4"
        counter
        v-model="setYear"
        hide-details
      ></v-text-field>

      <v-spacer></v-spacer>
      <v-select
        :items="seasionItems"
        label="選擇季"
        v-model="setSeason"
        return-object
        hide-details
      ></v-select>
    </div>

    <div v-if="dataReady">
      <!-- {{ PDValue }} -->
      <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">數據</th>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold">公式</th>
            <th class="title font-weight-bold">目標值</th>
            <th class="title font-weight-bold" v-for="month in setSeason.months" :key="month.season">{{month}} 月</th>
            <th class="title font-weight-bold">季平均</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>--</td>
            <td>當月住民總人數(A)</td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a }}</td>
            <td>{{ (returnTotal(0,'a') / 3).toFixed(2) }}</td>
          </tr>
          <tr>
            <td>1</td>
            <td>當月全體住民轉至急性醫院住院人次(B):轉至急性醫院住院比率</td>
            <td>B/A</td>
            <td>
              <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.bTval" class="ma-0 pa-0"></v-text-field>%
              </div>
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td>{{ returnAverage('b','a') }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>
              當月住民總人日數(C)
            </td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c }}</td>
            <td>{{ (returnTotal(0,'c') / 3).toFixed(2) }}</td>
          </tr>
          <tr>
            <td>2</td>
            <td>
              當月全體住民轉至急性醫院住院人次(B):轉至急性醫院住院密度
            </td>
            <td>B/C</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b2Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b / list.c) * 100).toFixed(2) : 0 }} %</td>
            <td>{{ returnAverage('b','c') }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>
              當月轉至急性醫院住院住民之總人數(D)
            </td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d }}</td>
            <td>{{ (returnTotal(0,'d') / 3).toFixed(1) }}</td>
          </tr>
          <tr>
            <td>3</td>
            <td>當月非計畫性轉至急性醫院住院人次(E):非計畫性轉至急性醫院住院比率</td>
            <td>E/D</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b4Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e ? ((list.e / list.d) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('e','d') }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>當月新住民人數(F)</td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.f }}</td>
            <td>{{ (returnTotal(0,'f') / 3).toFixed(2) }}</td>
          </tr>
          <tr>
            <td>4</td>
            <td>當月新住民入住後72小時內非計畫性轉至急性醫院住院人次(G)；新住民入住後72小時內非計畫性轉至急性醫院住院比率</td>
            <td>G/F</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c1Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.g ? ((list.g / list.f) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('g','f') }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>當月非計畫性轉至急性醫院住院人數(H)</td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.h }}</td>
            <td>{{ (returnTotal(0,'h') / 3).toFixed(2) }}</td>
          </tr>
          <tr>
            <td>5.1</td>
            <td>當月因心血管代償機能減退而非計畫性至急性醫院住院人次(I1)；因心血管代償機能減退而非計畫性至急性醫院住院比率</td>
            <td>I1/H</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c3Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.i1 ? ((list.i1 / list.h) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('i1','h') }} %</td>
          </tr>
          <tr>
            <td>5.2</td>
            <td>當月因骨折之治療或評估而非計畫性至急性醫院住院人次(I2)；因骨折之治療或評估而非計畫性至急性醫院住院比率</td>
            <td>I2/H</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c3Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.i2 ? ((list.i2 / list.h) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('i2','h') }} %</td>
          </tr>
          <tr>
            <td>5.3</td>
            <td>當月因腸胃道出血而非計畫性轉至急性醫院住院人次(I3)；因腸胃道出血而非計畫性轉至急性醫院住院比率</td>
            <td>I3/H</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c3Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.i3 ? ((list.i3 / list.h) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('i3','h') }} %</td>
          </tr>
          <tr>
            <td>5.4</td>
            <td>當月因感染而非計畫性至急性醫院住院人次(I4)；因感染而非計畫性至急性醫院住院比率</td>
            <td>I4/H</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c3Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.i4 ? ((list.i4 / list.h) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('i4','h') }} %</td>
          </tr>
          <tr>
            <td>5.5</td>
            <td>當月因其他內外科原因而非計畫性至急性醫院住院人次(I5)；因其他內外科原因而非計畫性至急性醫院住院比率</td>
            <td>I5/H</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c3Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.i5 ? ((list.i5 / list.h) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('i5','h') }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      <v-textarea label="評估分析" auto-grow v-model="list.note"></v-textarea>

      <div class="d-flex justify-space-between">
        <v-btn class="error" @click.stop="delData" v-if="workProcess == 'edit'"
          >刪除目前資料</v-btn
        >
        <v-spacer></v-spacer>
        <v-btn
          class="primary"
          @click.stop="saveData"
          v-if="workProcess == 'add'"
          :loading="loading"
          :disabled="loading"
          >資料存檔</v-btn
        >
        <v-btn
          class="success"
          @click.stop="saveData"
          v-if="workProcess == 'edit'"
          :loading="loading"
          :disabled="loading"
          >資料更新</v-btn
        >
      </div>

      <v-divider class="my-2"></v-divider>

      <!-- 圖表區 -->
      <Charts :setYear="setYear" :setSeason="setSeason" :list="list" />
      <!-- 圖表區 end-->
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import PDVFn from "@/assets/js/PDVFn.js";
import * as math from "mathjs"; //引入mathjs,處理浮點相乘產生的問題

import Charts from './seasonChart.vue'

export default {
  components:{ Charts },
  props: ["items"], //items全部約束事件資料
  data() {
    return {
      useDataBase : 'analysisofevaluationindicators',
      setYear: dayjs().format("YYYY"),
      setSeason: { season: 1, text: "1~3月", months: [1, 2, 3] },
      seasionItems: [
        { season: 1, text: "1~3月", months: [1, 2, 3] },
        { season: 2, text: "4~6月", months: [4, 5, 6] },
        { season: 3, text: "7~9月", months: [7, 8, 9] },
        { season: 4, text: "10~12月", months: [10, 11, 12] },
      ],
      list: {
        monthLists:[], //一次放三個月的資料
      }, 
      
      workProcess: "add", //判斷目前作業為新增或修改
      loading: false,
      rules: [v => v.length <= 4 || 'Max 4 characters'],
      
    };
  },

  computed: {
    //判斷資料都已讀入
    dataReady() {
      if (
        this.$store.state.userItems.length &&
        this.$store.state.bed_usedItems.length &&
        this.$store.state.behospitalizedItems.length &&
        this.$store.state.analysisofevaluationindicatorsItems
      ) {
        this.checkType(); //判斷新增/修改
        return true;
      } else {
        console.log("not yet");
        return false;
      }
    },
    
  },

  methods:{
        //計算加總
    returnTotal(num,key1,key2=null){
      if (key2){
        //有兩個key值時是需要經過計算產的的值才能加總
        let total = 0;
        this.list.monthLists.forEach(list=>{
          let val = list[key1] ? math.format(list[key1]/list[key2]*100) : 0
          total = math.add(total,val)  
        })
        return total.toFixed(num);
      }else{
        //只有一個值的總計計算
        return this.list.monthLists.reduce((total,i)=>{
          return total += parseInt(i[key1])
        },0)
      }
    },
    //計算平均
    returnAverage(key1,key2){
      let total = 0;
      this.list.monthLists.forEach(list=>{
        let val = list[key1] ? math.format(list[key1]/list[key2]*100) : 0
        total = math.add(total,val)  
      })
      return math.format(total / 3,2);
    },
    //判斷新增/修改
    checkType() {
      let match = this.$store.state.analysisofevaluationindicatorsItems
        .map((i) => {
          return {
            snkey: i.snkey,
            ...JSON.parse(i.datalist),
          };
        })
        .find((i) => {
          return i.range.setYear == this.setYear && i.range.setSeason.text == this.setSeason.text && i.rangeType == "season" && i.analysisKind == this.analysisKind;;
        });
      if (match) {
        //資料存在->修改
        this.workProcess = "edit";
        this.list = match;
      } else {
        //新增
        this.workProcess = "add";
        this.newData();
      }
    },
    // 建構未存檔之分析結果
    newData() {
      this.list = {
        monthLists : []
      }
      this.setSeason.months.forEach(month=>{
        let newMonth = dayjs(`${this.setYear}-${month}`).format("YYYY-MM");
        this.list.monthLists.push(PDVFn.returnMonthDataUnplannedEvent(this.items, newMonth));  //各個月資料的陣列
      })
    },
    //新增/修改
    saveData() {
      this.loading = true;
      if (this.workProcess == "add") {
        //新增
        //這裡的range設定把年設定及季的設定都存成物件檔去比對
        this.list.range = {
          setYear : this.setYear,
          setSeason : this.setSeason
        };
        this.list.rangeType = "season";
        this.list.create_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          datalist: JSON.stringify(this.list),
        };
        var url = "general/add/";
        this.workProcess = "edit";
      } else {
        //修改
        if (!this.list.edit_man) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man;
        var postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: "pass",
        };
        var url = "general/edit/";
      }

      this.$api.options(
        url + this.$store.state.databaseName + "/" + this.useDataBase,
        postData
      ).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "已存檔", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.loading = false;
          this.$store.dispatch("getItems", "analysisofevaluationindicators");  //更新已存檔的分析資料
        }
      });
    },
    //刪除功能
    delData() {
      let delData = this.list;
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: this.useDataBase,
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.newData();
              this.workProcess = "add";
              this.$store.dispatch("getItems", "analysisofevaluationindicators");  //更新已存檔的分析資料
            }
          });
        }
      });
    },
  },
};
</script>