<template>
  <div class="InfectionEventAnalysisMonth">
    <div class="d-flex justify-space-between align-center">
      <v-text-field
        type="month"
        label="選擇年/月"
        v-model="setMonth"
        hide-details
      ></v-text-field>

      <v-spacer></v-spacer>
    </div>
    <div v-if="dataReady">
      <!-- {{ PDValue }} -->
      <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">數據</th>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold">件數</th>
            <th class="title font-weight-bold">公式</th>
            <th class="title font-weight-bold">比率(%)</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>--</td>
            <td>當月住民總人日數(A)</td>
            <td>{{ list.a }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>1</td>
            <td>當月總感染人次(B):總感染發生密度</td>
            <td>{{ list.b }}</td>
            <td>B/A</td>
            <td>{{ list.b ? ((list.b / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>2</td>
            <td>當月呼吸道感染人次(C):呼吸道感染發生密度</td>
            <td>{{ list.c }}</td>
            <td>C/A</td>
            <td>{{ list.c ? ((list.c / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>2.1</td>
            <td>當月下呼吸道感染人次(C1):下呼吸道感染發生密度</td>
            <td>{{ list.c1 }}</td>
            <td>C1/A</td>
            <td>{{ list.c1 ? ((list.c1 / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>3</td>
            <td>當月泌尿道感染人次(D):泌尿道感染發生密度</td>
            <td>{{ list.d }}</td>
            <td>D/A</td>
            <td>{{ list.d ? ((list.d / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>當月使用存留導尿管總人日數(D1)</td>
            <td>{{ list.d1 }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>--</td>
            <td>當月未使用存留導尿管總人日數(D2)</td>
            <td>{{ list.d2 }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>3.1</td>
            <td>當月使用存留導尿管的泌尿道感染人次(D3):使用存留導尿管的泌尿道感染發生密度</td>
            <td>{{ list.d3 }}</td>
            <td>D3/D1</td>
            <td>{{ list.d3 ? ((list.d3 / list.d1) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>3.2</td>
            <td>當月未使用存留導尿管的泌尿道感染人次(D4):未使用存留導尿管的泌尿道感染發生密度</td>
            <td>{{ list.d4 }}</td>
            <td>D4/D2</td>
            <td>{{ list.d4 ? ((list.d4 / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>

          <tr>
            <td>4</td>
            <td>當月皮膚之感染人次(E):皮膚感染發生密度</td>
            <td>{{ list.e }}</td>
            <td>E/A</td>
            <td>{{ list.e ? ((list.e / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>4.1</td>
            <td>當月皮膚及軟組織感染(蜂窩性組織炎/軟組織/傷口感染)人次(E1):皮膚及軟組織感染(蜂窩性組織炎/軟組織/傷口感染)發生密度</td>
            <td>{{ list.e1 }}</td>
            <td>E1/A</td>
            <td>{{ list.e1 ? ((list.e1 / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>4.2</td>
            <td>當月疥瘡感染人次(E2):疥瘡感染發生密度</td>
            <td>{{ list.e2 }}</td>
            <td>E2/A</td>
            <td>{{ list.e2 ? ((list.e2 / list.a) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>5</td>
            <td>當月腸胃道感染人次(F):腸胃道感染發生密度</td>
            <td>{{ list.f }}</td>
            <td>F/A</td>
            <td>{{ list.f ? ((list.f / list.a) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      <v-textarea label="評估分析" auto-grow v-model="list.note"></v-textarea>

      <div class="d-flex justify-space-between">
        <v-btn class="error" @click.stop="delData" v-if="workProcess == 'edit'"
          >刪除目前資料</v-btn
        >
        <v-spacer></v-spacer>
        <v-btn
          class="primary"
          @click.stop="saveData"
          v-if="workProcess == 'add'"
          :loading="loading"
          :disabled="loading"
          >資料存檔</v-btn
        >
        <v-btn
          class="success"
          @click.stop="saveData"
          v-if="workProcess == 'edit'"
          :loading="loading"
          :disabled="loading"
          >資料更新</v-btn
        >
      </div>
    </div>

  </div>
</template>

<script>
import dayjs from "dayjs";
import PDVFn from "@/assets/js/PDVFn.js";

export default {
  props: ["items","analysisKind","matchpipelineItems"], 
  //items全部感染事件資料;matchpipelineItems管路紀錄中,符合導尿管條件的紀錄;analysisKind 計錄時的類別;因為全部放到analysisofevaluationindicators資料庫裡了
  data() {
    return {
      useDataBase : 'analysisofevaluationindicators',
      list: {},
      setMonth: dayjs().format("YYYY-MM"),
      workProcess: "add", //判斷目前作業為新增或修改
      loading: false,
    };
  },

  computed: {
    //判斷資料都已讀入
    dataReady() {
      if (
        this.$store.state.userItems.length &&
        this.$store.state.bed_usedItems.length &&
        this.$store.state.behospitalizedItems.length &&
        this.$store.state.analysisofevaluationindicatorsItems && 
        this.matchpipelineItems
      ) {
        this.checkType(); //判斷新增/修改
        return true;
      } else {
        console.log("not yet");
        return false;
      }
    },
  },

  methods: {
    //判斷新增/修改
    checkType() {
      let match = this.$store.state.analysisofevaluationindicatorsItems
        .map((i) => {
          return {
            snkey: i.snkey,
            ...JSON.parse(i.datalist),
          };
        })
        .find((i) => {
          return i.range == this.setMonth && i.rangeType == "month" && i.analysisKind == this.analysisKind;
        });
      if (match) {
        //資料存在->修改
        this.workProcess = "edit";
        this.list = match;
      } else {
        //新增
        this.workProcess = "add";
        this.newData();
      }
    },
    // 顯示分析結果 之後需要存檔 -> 要做判斷 ? 好像.都直接算出來就好了.
    newData() {
      this.list = PDVFn.returnMonthDataInfectionEvent(this.items, this.setMonth, this.matchpipelineItems);
    },
    //新增/修改
    saveData() {
      this.loading = true;
      if (this.workProcess == "add") {
        //新增
        this.list.analysisKind = this.analysisKind
        this.list.range = this.setMonth;
        this.list.rangeType = "month";
        this.list.create_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          datalist: JSON.stringify(this.list),
        };
        var url = "general/add/";
        this.workProcess = "edit";
      } else {
        //修改
        if (!this.list.edit_man) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man;
        var postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: "pass",
        };
        var url = "general/edit/";
      }

      this.$api.options(
        url + this.$store.state.databaseName + "/" + this.useDataBase,
        postData
      ).then((rs) => {
        console.log(rs);
        if (rs.state == 1) {
          var pop = { msg: "已存檔", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.loading = false;
          this.$store.dispatch("getItems", "analysisofevaluationindicators");  //更新已存檔的分析資料
        }
      });
    },
    //刪除功能
    delData() {
      let delData = this.list;
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: this.useDataBase,
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.newData();
              this.workProcess = "add";
              this.$store.dispatch("getItems", "analysisofevaluationindicators");  //更新已存檔的分析資料
            }
          });
        }
      });
    },
  },
};
</script>

<style scoped>
.v-data-table td {
  font-size: 1rem;
}
</style>