<template>
  <div>
      <!-- 跌倒發生密度 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年第{{ setSeason.season }}季跌倒發生密度</h1>
        <VeLine :data="chartData1" :settings="chartSetNormal"></VeLine>
      </v-alert>

      <!-- 跌倒發生原因 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年第{{ setSeason.season }}季跌倒發生原因</h1>
        <VeHistogram :data="chartData2" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold" v-for="month in setSeason.months" :key="month.season">{{month}} 月</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>
              住民健康狀況
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b1 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              治療或藥物反應
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b2 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              環境中危險因子
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b3 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>其他因素</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b4 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>

      <!-- 跌倒傷害嚴重度 -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}年第{{ setSeason.season }}季跌倒傷害嚴重度</h1>
        <VeHistogram :data="chartData3" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold">指標項目</th>
            <th class="title font-weight-bold" v-for="month in setSeason.months" :key="month.season">{{month}} 月</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>1級</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c1 ? ((list.c1 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>2級</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c2 ? ((list.c2 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>3級</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c3 ? ((list.c3 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear', 'setSeason' ,'list'],

  data(){
    return {
      // 圖表設定及值
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },

  computed:{
    //圖表1的值
    chartData1(){
      return {
        columns: ["月份", "跌倒發生密度"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : this.setSeason.months[index]+'月',
            '跌倒發生密度' : i.b ? (i.b / i.a).toFixed(4) : 0,
          }
        })
      }
    },
    //圖表2的值
    chartData2(){
      return {
        columns: ["月份", "住民健康狀況", "治療或藥物反應", "環境中危險因子" ,"其他因素"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : this.setSeason.months[index]+'月',
            '住民健康狀況' : i.b ? (i.b1 / i.b) : 0,
            '治療或藥物反應' :i.b ? (i.b2 / i.b) : 0,
            '環境中危險因子' : i.b ? (i.b3 / i.b) : 0,
            '其他因素' : i.b ? (i.b4 / i.b) : 0,
          }
        })
      }
    },
    //圖表3的值
    chartData3(){
      return {
        columns: ["月份", "1級", "2級", "3級"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '月份' : this.setSeason.months[index]+'月',
            '1級' : i.c1 ? (i.c1 / i.c) : 0,
            '2級' :i.c2 ? (i.c2 / i.c) : 0,
            '3級' : i.c3 ? (i.c3 / i.c) : 0,
          }
        })
      }
    },
  }
}
</script>